@import url(https://fonts.googleapis.com/css?family=Questrial);
html, body {
  min-width: 250px;
  margin: 0;
  padding: 0;
  font-family: 'Questrial', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 16px;
  
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

*,
::after,
::before {
  box-sizing: border-box;
}

header {
  background-color: #680ae2;
  display: flex;
  align-items: center;
  width: 100%;
  position: fixed;
  z-index: 100;
}

nav {
  width: 100%;
  display: flex;
  align-items: center;
  padding: 15px;
  justify-content: space-between;
}

.nav-link {
  padding: 0 0.5rem;
  text-decoration: none;
  color: white;
  cursor: pointer;
}

.jam-logo {
  margin-left: auto;
  margin-right: auto;
  padding: 0 10px;
  width: 120px;
}

.container-title {
  color: white;
  font-size: 4rem;
  text-shadow: 5px 5px 10px #181818;
}

.container {
  background-image: url(/bloc-jams-react/static/media/landingpage.244bd765.jpg);
  background-position: center;
  height: 800px;
  width: 100%;
  background-size: cover;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.selling-points {
  display: flex;
  max-width: 1125px;
  margin: 0 auto;
  padding-right: 15px;
  padding-left: 15px;
}

.selling-points-left {
  width: 50%;
  height: 50%;
  padding: 2rem !important;
}

.selling-points-right {
  text-align: left;
  padding: 2rem !important;
}

.point {
  padding-bottom: 0.5rem;
}

.point-title {
  font-size: 1.25rem;
}

.title {
  margin-top: 2rem;
  font-size: 2rem;
}

@media only screen and (max-width: 800px) {
  .selling-points {
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  .selling-points-left {
    width: 100%;
    padding: 0.5rem;
  }

  .container-title {
    font-size: 2.25rem;
    margin: 0 1rem;
  }
}

.landing_btn {
  background-color: #680ae2;
  border-radius: 8px;
}

.library {
    background-color: #680AE2;
    width: 100%;
    height: 900px;
    margin: auto;
    color: white;
}

.row {
    margin: 4rem auto;
    display: flex;
    max-width: 1140px;
}

.column {
    position: relative;
    margin: auto;
    width: 400px;
    height: 400px;
    overflow: hidden;
    border-radius: 5px;
    box-shadow: 5px 5px 55px rgba( 0, 0 , 0, .4);
}

.column-title {
    font-size: 1.1rem;
    color: rgb(44, 44, 44);
}

.column-artist {
    font-size: 0.95rem;
    padding: 10px 0;
}

.column-info {
    height: 115px;
    width: 100%;
    position: absolute;
    padding: 1rem;
    bottom:0;
    background: rgb(255, 255, 255);opacity:0.77;
    -webkit-transform: translateY(35px);
            transform: translateY(35px);
    -webkit-transition: all .5s ease-in-out;
    transition: all .5s ease-in-out;
    text-decoration: none;
  }

  #column-cover {
    bottom: 100px;
    left: -50px;
    position: relative;
  }

  @media only screen and (max-width: 950px) {
    .library {
        height: 800px;
    }

    .row {
        flex-direction: column;
        margin: 3rem auto;
    }

    .column {
        margin-top: 20px;
        margin-bottom: 20px;
        width: 300px;
        height: 300px;
    }
  }

.album {
    background-color: #680AE2;
    color: white; 
    background-size: cover;
    background-position: center;
    height: 100%;
    background-blend-mode: lighten;
}

.bg {
    background-color: #680AE2;
    height: 100%;;
}

#album-info {
    max-width: 1125px;
    margin: 0 auto;
}

#album-cover-art {
    max-width: 60%;
}

#empty-space {
    height: 10rem;;
}

#album-title {
    font-size: 1.25rem;
}

.artist {
    font-size: 0.85rem;
}

@media only screen and (max-width: 900px) {
    .album {
        padding: 3rem 0;    
    }

       #empty-space {
        height: 2rem;;
    }

    #album-cover-art {
        width: 70%;;
    }

    button {
        padding: 0.5rem 1rem;
        font-size: 1rem;
    }

    .wrap-container {
        display: none;
    }

    #buttons, #volume-control {
        width: 100%!important;
        padding: 0!important;
    }
}

@media only screen and (max-width: 1350px) {
    #song-list {
        position: static!important;
        margin: 2rem auto;
    }
}

.player-bar {
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    padding: 1rem;
    background-color: #680AE2;
    z-index: 100;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between
}

#time-control {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.current-time, .total-time {
    margin: 0 1rem;
}

#volume-control {
    display: flex;
    align-items: center;
    justify-content: center;
    padding-left: 80px;
}

.seek-bar {
    width: 100%;
    height: 0.25rem;
}

#wrap-container, #buttons, #volume-control {
    width: 30%;
}

.icon {
    margin: 0 1rem;
}

.ion-md-volume-high {
    padding: 0 1rem;
}

button {
    display: inline-block;
    border: none;
    padding: 1rem 2rem;
    margin: 0;
    background: none;
    color: #ffffff;
    font-size: 1.5rem;
    line-height: 1;
    cursor: pointer;
}

button:focus {
    background: none;
}

button:active{
    outline: none;
    border: none;
}

.volume {
    display: none;   
}

#volume-control:hover .volume {
    display: inline-block;
}

#number {
    font-size: 1rem;
}


#song-list {
    position: absolute;
    top: 125px;
    padding: 0 10px;
    left: 80px;
}

table {
    border-collapse: collapse; 
    font-family: 'Questrial', sans-serif;
    cursor: pointer;
}

tr td {
    border-bottom: 1px solid rgb(255, 255, 255);
    text-align: left;
}

#song-duration, #song-title {
    padding: 1rem 2rem;
    margin: 0;
}
footer {
    background-color: #680AE2!important;
    padding: 1rem!important;
    width: 100%;
    margin: 0 auto;
}

.card {

    padding-right: 15px;
    padding-left: 15px;
    margin: 2rem auto;
    
}

.card p {
    color: white;
    margin: 0!important;
    font-size: 0.9rem;
}
